
.bookList h1{
  text-align: center;
}

.bookList{
  width: 100%;
}


table {
  width: 80%; 
  border-collapse: separate; 
  border-spacing: 20px; 
  margin: 20px auto; 
  text-align: center;
}


td, th {
  padding: 10px; 
  border: 1px solid #aaa;
  width: 300px; 
  border-radius: 10px;
}

.op span {
  margin: auto 10px;
}

.showBook{
  margin-top: 50px;
  text-align: center;
  box-shadow: 0px 0px 10px 10px rgb(142, 131, 131);
  padding: 50px 100px;
}

.add{
  width: 260px;
  height: 75px;
  padding: 10px;
  background-color: green;
  color: white;
  font-size: 20px;
  border: none;
  outline: none;
  text-align: center;
  margin-left: 42.5%;
}

.form{
  /*height: 60vh;*/
  width: 50vw;
  display: flex;
  font-size: 20px;
   flex-direction: column;
   box-shadow: 0px 0px 10px 10px rgb(185, 166, 166);
   padding: 100px;
   margin-top:5vh;
   margin-bottom: 30vh;
   background-color: green;
   
}

.form input{
  margin:20px 0px;
  padding: 10px 18px;
  border-radius: 7px;
}

.form button{
  background: green;
  border-radius: 6px;
  outline: none;
  border: none;
  color: white;
  width: 240px;
  height: 50px;
  margin: 30px 0px;
  font-size: 20px;
}


.delete{
  text-align: center;
}

.delete button{
  padding: 20px;
  font-size: 20px;
  margin: 20px;
  width: 200px;
}

/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
}


.App {
 
  background-size: cover;
  mix-blend-mode: overlay;
  background-color: pink; 
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

