.Hero{
    display: flex;
    justify-content: space-between;
    background-image: url("../assets/gym_image2.jpg");
    background-size: cover;
    flex-direction: row;
   
}

.btn{
    width: 150px;
    height: 30px;
    border-radius: 10px;
    font-size: 25px;
    background-color: skyblue;
}

.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 1 1;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

.right-h{
    flex: 1 ;
    position: relative;
    
    
}

.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.the-best-ad> span{
    z-index: 2;
    
}

.calories> span{
    font-size: 20px;
    color: white;
}

.the-best-ad> div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
}

.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text> div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
}

.figures{
    display: flex;
    gap: 2rem;
}

.figures> div{
    display: flex;
    flex-direction: column;

}

.figures> div> span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}

.figures> div> span:nth-of-type(2){
    color: var(--gray);
    text-transform: uppercase;
}

.hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}

.hero-buttons> :nth-child(1){
    color: white;
    background-color: var(--orange);
    width: 8rem;
}

.hero-buttons> :nth-child(2){
    color: white;
    background-color: transparent;
    width: 8rem;
    border: 2px solid var(--orange);
}

.right-h> .btn{
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
}

.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}

.heart-rate> img{
    width: 2rem;
}

.heart-rate>:nth-child(2){
    color: var(--gray);
}

.heart-rate>:nth-child(3){
    color: white;
    font-size: 1.5rem;
}

.hero-image{
    position: absolute;
    top: 10rem;
    right: 8rem;
    width: 23rem;
}

.hero-image-back{
    position: absolute;
    top: 4rem;
    right: 20rem;
    z-index: -1;
    width: 15rem;
}

.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    top: 32rem;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    right: 28rem;
}

.bot{
    width: 200px;
    height: 200px;
    background-color: white;
    border-radius: 15px;
    scale: .05;
    rotate: 0deg;
    transition: all 0.5s;
    background-color: blue;
}

.bot:hover{
    scale: 1;
    rotate: 180deg;
    
}

.spin{
    height: 30px;
    width: 30px;
    border: 7px solid;
    border-radius: 50%;
    border-color: red black black black;
    animation: rotate  1.5s linear infinite;

}

@keyframes rotate{
    to {transform: rotate(365deg);}
}

.butto:before{
    animation: rotate 2s infinite linear;
    filter: blur(20px);
}

.buttoinner{
    position: relative;
    display: flex;
    width: 100%;
    z-index: 1;
    transition: all 0.18s ease;
    backdrop-filter: blur(40px);
    backface-visibility: hidden;
}

.calories> img{
    width: 3rem;
}

.calories> div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.calories> div>:nth-child(1){
     color: var(--gray);
}

.calories> div>:nth-child(2){
     color: white;
     font-size: 1.5rem;
}

.hero-blur{
    width: 100%;
    height: 10px;
    left: 0;
}

@media screen and(max-width: 786px) {

    .hero{
        flex-direction: column;
    }

    .hero-blur{
        width: 14rem;
    }

    .the-best-ad{
        margin-top: 0;
        font-size: small;
        align-self: center;
         
    }
    
    .hero-text{
        font-size: xx-large;
        align-items: center;
        justify-content: center;
    }
}